import AboutCard from "components/aboutCard/AboutCard";
import Heading from "components/heading/Heading";
import styles from "./About.module.scss";

interface IAboutCard {
  headingText: string;
  paragraphText: string;
}

const cardsContent: IAboutCard[] = [
  {
    headingText: "Zaadoptuj idealnego zwierzaka",
    paragraphText:
      "Dziki appce duo atwiej zaadaptujesz samotne zwierzaki. Znalezienie i wybranie Twojego nowego pupila s teraz jeszcze atwiejsze  moesz to zrobi nie wychodzc z domu!",
  },
  {
    headingText: "Wybierz idealny wolontariat",
    paragraphText:
      "Korzystajc z appki wejdziesz do spoecznoci osb, ktrym nie jest obojtny los zwierzakw. Razem moemy pomc jeszcze wikszej iloci opuszczonych zwierzakw.",
  },
  {
    headingText: "Zadbaj o swojego pupila",
    paragraphText:
      "Oprcz adoptowania samotnych zwierzt, moesz rwnie w niej prowadzi dokumentacj medyczn swojego pupila i kontaktowa si z innymi uytkownikami.",
  },
  {
    headingText: "Odnajd ukochan zgub",
    paragraphText:
      "Czy zdarzyo ci si kiedy, e Twj ukochany pies albo kot uciek lub znikn? Niestety zdarza si to do czsto. Dlatego postanowilimy wykorzysta dziaanie appki oraz yczliwo spoecznoci, do wsplnego rozwizywania takich problemw!",
  },
];

const About = () => {
  return (
    <div className={styles.about_container}>
      <Heading>O naszej aplikacji</Heading>
      <div className={styles.content_container}>
        {cardsContent.map((card, index) => {
          return (
            <AboutCard
              key={`about-card-${index}`}
              headingText={card.headingText}
              paragraphText={card.paragraphText}
            />
          );
        })}
      </div>
    </div>
  );
};

export default About;
