import styles from "./HowTo.module.scss";
import phonesImage from "img/phones.png";
import Heading from "components/heading/Heading";

const HowTo = () => {
  return (
    <div className={styles.howTo_container}>
      <div className={styles.howTo_content}>
        <Heading>Podaj swoj apk zwierzakom!</Heading>
        <p className={`grey_paragraph ${styles.howTo_paragraph}`}>
          Misja, ktra przywieca appce, zwizana jest z chci niesienia
          pomocy zwierzakom oraz caej spoecznoci, dla ktrej losy bezbronnych
          zwierzt nie s obojtne. Osb chccych pomaga jest wiele. Czasami
          jednak warunki oraz indywidualne moliwoci utrudniaj adoptowanie
          pupili. Postanowilimy wyj naprzeciw tym potrzebom oraz znacznie
          usprawni cao procesu. Dziki appce przygarnianie samotnych psw,
          kotw i innych zwierzt domowych bdzie tak proste, jak kilka klikni
          w telefonie. Aplikacja uatwi take znajdowanie zaginionych pupili,
          dziki wymianie informacji pomidzy czonkami spoecznoci. Dodatkowo,
          kady uytkownik bdzie mg w niej prowadzi kart zdrowia swojego
          ukochanego zwierzaka.
          <span className={styles.paragraphBold}>
            Razem moemy pomc jeszcze wikszej iloci zwierzt w
            potrzebie!
          </span>
        </p>
      </div>
      <div className={styles.howTo_image_container}>
        <img src={phonesImage} alt="Phones" />
      </div>
    </div>
  );
};

export default HowTo;
