import styles from "./Aspects.module.scss";
import AspectCard from "components/aspectCard/AspectCard";
import magnifier from "img/Icons/magnifier-icon.svg";
import petHouse from "img/Icons/pet-house-icon.svg";
import share from "img/Icons/share-icon.svg";
import heart from "img/Icons/heart-icon.svg";
import safetyRing from "img/Icons/safety-ring-icon.svg";
import eye from "img/Icons/eye-icon.svg";
import location from "img/Icons/location-icon.svg";
import emptyHeart from "img/Icons/empty-heart-icon.svg";
import Lappka from "components/lappka/Lappka";
import Heading from "components/heading/Heading";

interface IAspectCard {
  icon: string; //icon url
  paragraphText: string;
}

const aspectData: IAspectCard[] = [
  {
    icon: magnifier,
    paragraphText:
      "Wyszukiwanie zwierzakw w oparciu o Twoje preferencje co do ich typw (pies, kot, winka morska itd.) oraz w wybranej lokalizacji (pooenie schroniska wzgldem Ciebie itp.)",
  },
  {
    icon: petHouse,
    paragraphText:
      "Podstawowe informacje na jego temat wszystkich zwierzakw, jak: odlego do schroniska, w ktrym przebywa, gatunek, ma, wiek, pe, status sterylizacji oraz informacje o usposobieniu zwierzcia",
  },
  {
    icon: share,
    paragraphText:
      "Moliwo udostpniania profili zwierzakw w mediach spoecznociowych",
  },
  {
    icon: heart,
    paragraphText:
      "Cyfrowa ksieczka zdrowia pupila, w ktrej moesz aktywnie ledzi stan szczepie, wizyt u weterynarza, przepisywanych lekarstw, odbytych operacji oraz innych kwestii zwizanych ze zdrowiem pupila",
  },
  {
    icon: safetyRing,
    paragraphText:
      "Moliwo wyboru rnych wariantw wolontariatu, zwizanego z pomoc dla schronisk i zwierzakw",
  },
  {
    icon: eye,
    paragraphText:
      "Zgaszanie zaginionych pupili, z moliwoci korespondowania midzy czonkami spoecznoci oraz moliwoci udostpniania lokalizacji",
  },
  {
    icon: location,
    paragraphText:
      "Raportowanie bkajcych si zwierzt wraz z moliwoci udostpniania ich lokalizacji w aplikacji (informowanie schronisk i sub zajmujcych si opuszczonymi zwierztami)",
  },
  {
    icon: emptyHeart,
    paragraphText:
      "Moliwo zapisywania przegldanych profili w kategorii Ulubione, tak aby w wygodny sposb przeglda je pniej",
  },
];

const Aspects = () => {
  return (
    <div className={styles.aspects_container}>
      <Heading>
        Dlaczego <Lappka />?
      </Heading>
      <div className={styles.content_container}>
        {aspectData.map((aspect, index) => {
          return (
            <AspectCard
              key={`aspect-card-${index}`}
              icon={aspect.icon}
              paragraphText={aspect.paragraphText}
            />
          );
        })}
      </div>
    </div>
  );
};

export default Aspects;
